<template>
  <div class="upload-content-unit">
    <div class="upload-content-unit-bg">
      <div class="line1"></div>
      <div class="line2"></div>
    </div>
    <input
      type="file"
      accept="image/*"
      id="file"
      class="photo-input"
      @change="addNewPhoto($event,data.id)"
    />
    <div class="photo-show-pic" :style="this.data.value"></div>
  </div>
</template>

<script>
export default {
    name: 'UploadImgUnit',
    data: function() {
        return {};
    },
    methods: {
        addNewPhoto: function(e, id) {
            var that = this;
            var file = e.target.files[0];
            if (file.type.indexOf('image') == 0) {
                var reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function(e) {
                    // 图片base64化
                    var newUrl = this.result;
                    console.log(newUrl);

                    that.$bus.emit('upload-img-unit', {
                        name: that.name,
                        id: id,
                        value:
                            'background:url(' +
                            newUrl +
                            ');backgroundSize:cover',
                    });
                    return;
                    that.pageInfo.picData[
                        that.pageInfo.picData.length - 1
                    ].value =
                        'background:url(' + newUrl + ');backgroundSize:cover';
                    var obj = { id: that.pageInfo.picData.length, value: '' };
                    that.pageInfo.picData.push(obj);
                };
            }
        },
    },
    props: ['data', 'name'],
};
</script>
<style scoped>
.upload-content-unit {
    width: 42vw;
    height: 24vw;
    position: relative;
    display: inline-block;
    border: 1px solid #bbb;
}
.upload-content-unit-bg {
    width: 100%;
    height: 100%;
    position: absolute;
}
.line1 {
    width: 80%;
    border-top: 1px solid #bbb;
    position: absolute;
    box-sizing: border-box;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.line2 {
    height: 80%;
    border-left: 1px solid #bbb;
    position: absolute;
    box-sizing: border-box;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.photo-input {
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    cursor: pointer;
    z-index: 5;
}
.photo-show-pic {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-size: cover;
}
.video-show-pic {
    width: 100%;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}
</style>


