<template>
  <div class="back-part">
    <img src="../../public/imgs/back.png" class="back-btn" />
    <div class="click-zone" @click="returnBack"></div>
  </div>
</template>

<script>
export default {
    name: 'BackBtn',
    methods: {
        returnBack: function() {
            this.$bus.emit('return', { page: this.pagenum });
        },
    },
    props: ['pagenum'],
};
</script>

<style scoped>
.back-part {
    width: 100%;
    height: 10vw;
    background: #fff;
    margin-bottom: 2vw;
    position: relative;
}
.back-btn {
    width: 5vw;
    position: absolute;
    left: 5vw;
    top: 3vw;
}
.click-zone {
    width: 30%;
    height: 100%;
    position: absolute;
    left: 0;
}
</style>


