<template>
  <div class="selector-part" @click="showSelect">
    <div class="input-name title1">{{data.title}}</div>
    <div class="input-result">{{selectedResult}}</div>
  </div>
</template>
<script>
import '../util/iosSelect/iosSelect.css';
import '../util/iosSelect/iosSelect.js';
export default {
    name: 'SelectorOne',
    data: function() {
        return {};
    },
    props: ['data'],
    computed: {
        selectedResult: function() {
            var str = '';
            if (this.data.autoResult) {
                if (!this.data.selected) {
                    for (var i = 0; i < this.data.selectedList.length; i++) {
                        str += this.data.selectedList[i].value;
                        if (i < this.data.selectedList.length - 1) {
                            str += '/';
                        }
                    }
                    return str;
                } else {
                    this.data.selectedList.forEach(element => {
                        if (element.id == this.data.selected) {
                            str = element.value;
                        }
                    });
                    return str;
                }
            } else {
                return this.data.result;
            }
        },
    },
    methods: {
        //地址显示控件点击后触发事件
        showSelect: function() {
            //自己实现的根据地址名称获取id，代码如下
            var that = this;

            var iosSelect = new IosSelect(1, [this.data.selectedList], {
                title: this.data.title,
                itemHeight: 35,
                // relation: [1], //三个初始值，省市县
                // oneLevelId: '10000',
                callback: function(data) {
                    that.$bus.emit('selectone', {
                        name: that.data.name,
                        value: data,
                    });
                    console.log(data);
                },
            });
        }, //根据地址名字转化未三级level，用以给iosSelect设置初始值
    },
};
</script>

<style scoped>
.selector-part {
    width: 100%;
    height: 10vw;
    background: #fff;
    position: relative;
}
.input-name {
    width: 30%;
    height: 100%;
    position: absolute;
    left: 0;
    padding-left: 5vw;
    line-height: 10vw;
}
.input-result {
    width: 70%;
    height: 100%;
    position: absolute;
    right: 0;
    padding-right: 5vw;
    line-height: 10vw;
    font-weight: 400;
    font-size: 14px;
    color: rgb(176, 173, 174);
    font-style: normal;
    letter-spacing: 0px;
    text-align: right;
}
</style>
