<template>
  <div class="page sold-part">
    <BackBtn pagenum="2"></BackBtn>
    <SelectorOne :data="selectedData"></SelectorOne>
    <div class="upload-idcard">
      <div class="sold-part-title title1">请上传身份证正面（照片清晰、完整、不能用软件编辑）</div>
      <div class="upload-content">
        <UploadImgUnit
          v-for="item in picdata.list"
          :key="item.id"
          :data="item"
          :name="picdata.name"
        ></UploadImgUnit>
      </div>
    </div>
    <FormInput class="marginTopClass" :formdata="inputName"></FormInput>
    <FormInput class="marginTopClass" :formdata="inputIdNumber"></FormInput>
    <div class="upload-idcard">
      <div class="sold-part-title title1">营业执照（照片清晰、完整、不能用软件编辑）</div>
      <div class="upload-content">
        <UploadImgUnit
          v-for="item in picdata2.list"
          :key="item.id"
          :data="item"
          :name="picdata2.name"
        ></UploadImgUnit>
      </div>
    </div>
    <img src="../../public/imgs/v2_pvyliw.png" class="page2-hint" />
    <div class="submit-btn" @click="submit">提交</div>
  </div>
</template>
<script>
import BackBtn from '../components/BackBtn';
import SelectorOne from '../components/SelectorOne';
import UploadImgUnit from '../components/UploadImgUnit';

import FormInput from '../components/FormInput';
export default {
    name: 'Page2SoldPart',
    data: function() {
        return {
            selectedData: {
                name: 'selectone-idselected',
                title: '身份选择',
                result: '公寓房东/职业房东/个人房东',
                autoResult: true,
                selected: null,
                selectedList: [
                    { id: '10000', value: '公寓房东' },
                    { id: '10001', value: '职业房东' },
                    { id: '10002', value: '个人房东' },
                ],
            },
            picdata: { name: 'uploadIdCard', list: [{ id: 0, value: '' }] },
            picdata2: { name: 'uploadIdCard2', list: [{ id: 0, value: '' }] },
            inputName: {
                id: 0,
                placeholder: '请输入姓名',
                title: '姓名：',
            },
            inputIdNumber: {
                id: 1,
                placeholder: '请输入身份证号',
                title: '身份证号',
            },
        };
    },
    methods: {
        submit: function() {
            this.$bus.emit('page2 submit', {});
        },
        updateSelected: function(data) {
            console.log(data);
            if (data.name == this.selectedData.name) {
                this.selectedData.selectedList.forEach(item => {
                    if (item.id == data.value.id) {
                        this.selectedData.selected = data.value.id;
                    }
                });
            }
            console.log(this.selectedData);
        },
        uploadImg: function(data) {
            if (data.name == this.picdata.name) {
                this.picdata.list[this.picdata.list.length - 1].value =
                    data.value;
                var obj = { id: this.picdata.length, value: '' };
                this.picdata.list.push(obj);
            } else if (data.name == this.picdata2.name) {
                this.picdata2.list[this.picdata2.list.length - 1].value =
                    data.value;
                var obj = { id: this.picdata2.length, value: '' };
                this.picdata2.list.push(obj);
            }
        },
    },
    created: function() {
        this.$bus.on('selectone', this.updateSelected);
        this.$bus.on('upload-img-unit', this.uploadImg);
        console.log(this.picdata);
    },
    components: { BackBtn, SelectorOne, UploadImgUnit, FormInput },
};
</script>

<style scoped>
.upload-idcard {
    width: 100%;
    position: relative;
    background: #fff;
    margin-top: 1vw;
    padding: 5vw;
}
.upload-content {
    width: 100%;
    height: auto;
    position: relative;
    padding-top: 5vw;
}
.sold-part-title {
}
.page2-hint {
    width: 100%;
    position: relative;
}
</style>


